$darkgeryBlue: hsl(217, 19%, 24%);
$lightCyan: hsl(193, 38%, 86%);
$neonGreen: hsl(150, 100%, 66%);

// * {
//     outline: 2px solid wheat;
// }
.mainContainer {
    width: 90%;
    margin: auto;
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.container {
    background-color: $darkgeryBlue;
    min-height: 14rem;
    width: 40%;
    height: max-content;
    border-radius: 10px;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
    padding-top: 1.5rem;
    padding-bottom: 1.5rem;
    text-align: center;
    position: relative;
}
.adviceId {
    color: $neonGreen;
    text-transform: uppercase;
    font-size: 0.8rem;
    letter-spacing: 3px;
}
.advice {
    color: $lightCyan;
    font-weight: bold;
    margin-top: 1rem;
    height: max-content;
}
.patternDivider {
    width: 90%;
    display: block;
    margin: auto;
    margin-top: 1rem;
    position: absolute;
    bottom: 2.5rem;
    left: 5%;
}
.iconDiceContainer {
    position: absolute;
    width: 2.5rem;
    background-color: $neonGreen;
    height: 2.5rem;
    border-radius: 50%;
    bottom: -1rem;
    left: 45.5%;
    cursor: pointer;
}
.iconDiceContainer:hover {
    box-shadow: 0px 0px 11px 16px rgb(0 0 0 / 20%), 0px 1px 11px 7px $neonGreen;
}
.iconDice {
    width: 50%;
    height: 50%;
    position: absolute;
    left: 0.6rem;
    top: 0.6rem;
}
.attribution {
    display: block;
    margin: auto;
    width: fit-content;
    background-color: white;
    margin-top: 2rem;
    text-align: center;
}
@media screen and (max-width: 715px) {
    .container {
        width: 50%;
        min-height: 14rem;
    }
}
// mobile design
@media screen and (max-width: 500px) {
    .container {
        width: 100%;
        height: max-content;
    }
}
